// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {scrollIntoView} from '../../sticky-scroll-into-view'

// Given a selected section (class .js-repo-team-selected) determine text for the summary button of the teams selector.
// Takes into account the selectedTeams session cache as well.
//
//  details - The details menu in which selected elements are stored.
//
//  Returns the names joined with ", " if under 3 names. If 3 or more names are selected, then returns `n teams selected`. If no teams are selected, returns null.
function textForTeams(details: HTMLElement): string | null {
  const selectedSection = details.querySelector<HTMLElement>('.js-repo-team-selected')!
  const selectedSectionInputs = selectedSection.querySelectorAll('input.js-team-selection-input:checked')

  const filterSection = details.querySelector<HTMLElement>('.js-repo-team-filter-results')!
  const checkedfilterInputs = filterSection.querySelectorAll('input.js-team-selection-input:checked')

  const lengthToCheck = selectedSectionInputs.length + checkedfilterInputs.length

  if (lengthToCheck === 0) {
    return null
  }

  if (lengthToCheck < 3) {
    const selectedInputNames = Array.from(selectedSectionInputs).map(input => input.getAttribute('data-display-name')!)
    const filteredInputNames = Array.from(checkedfilterInputs).map(input => input.getAttribute('data-display-name')!)
    const allItems = selectedInputNames.concat(filteredInputNames)
    const sortedItems = allItems.sort(function (a, b) {
      if (a.toUpperCase() < b.toUpperCase()) return -1
      if (a.toUpperCase() > b.toUpperCase()) return 1
      return 0
    })

    return sortedItems.join(', ')
  }

  return `${lengthToCheck} teams selected`
}

// Sets the summary button title to the appropriate text.
function setTeamButtonTitle(details: HTMLElement) {
  const summary = details.querySelector<HTMLElement>('.js-team-orgs-summary-text')!
  summary.textContent = textForTeams(details) || summary.getAttribute('data-default-label')!
}

// Commits selected teams from `js-repo-team-filter-results` to `js-repo-team-selected`.
// For all elements that are selected in `js-repo-team-filter-results`:
// 1. If the element is not already in `js-repo-team-selected`, we add it.
// 2. Removes  element  from `js-repo-team-filter-results`.
// After this, we sort the selected items to ensure previously selected items and the new ones are in order.
function commitSelectedTeams(details: HTMLElement) {
  const selectedSection = details.querySelector<HTMLElement>('.js-repo-team-selected')!

  // First, remove all unchecked elements from the selected section
  const uncheckedElements = selectedSection.querySelectorAll('.js-team-selection-input:not(:checked)')
  for (const uncheckedElement of uncheckedElements) {
    uncheckedElement.closest<HTMLElement>('.js-team-selection')!.remove()
  }

  const filterSection = details.querySelector<HTMLElement>('.js-repo-team-filter-results')!
  const checked = filterSection.querySelectorAll<HTMLInputElement>('.js-team-selection-input:checked')
  const initiallySelectedItems = selectedSection.querySelectorAll<HTMLInputElement>('.js-team-selection-input:checked')

  // Copy all elements that are checked into the selected section
  for (const checkedElement of checked) {
    // Make sure we don't already have this team in the selection
    const alreadyInSelection = Array.from(initiallySelectedItems).some(el => el.value === checkedElement.value)
    if (alreadyInSelection) continue

    const checkedLabel = checkedElement.closest<HTMLElement>('.js-team-selection')!
    const clonedElement = checkedLabel.cloneNode(true)
    selectedSection.prepend(clonedElement)
    checkedLabel.remove()
  }

  sortSelectedItems(selectedSection)
  setTeamButtonTitle(details)
}

// Sort child nodes of the selected section (`js-repo-team-selected`) by the data-name.
// This ensures that selected items are viewed in a nice coherent order.
function sortSelectedItems(selectedSection: HTMLElement) {
  const selectedItems = Array.from(selectedSection.querySelectorAll('.js-team-selection'))
  const sortedItems = selectedItems.slice().sort(function (a, b) {
    const nameA = a.getAttribute('data-name')!.toUpperCase()
    const nameB = b.getAttribute('data-name')!.toUpperCase()

    if (nameA < nameB) return 1
    if (nameA > nameB) return -1
    return 0
  })

  let child

  // Remove unsorted items from selected section
  while ((child = selectedItems.pop())) {
    selectedSection.removeChild(child)
  }

  // Add sorted items to selected section
  while ((child = sortedItems.pop())) {
    selectedSection.appendChild(child)
  }
}

// Given the details menu for team selections, we will compare the contents in `js-repo-team-selected`
// and `js-repo-team-filter-results`. We will ensure that no teams that appear in `js-repo-team-selected` will
// show up in `js-repo-team-filter-results` by comparing `data-name` attributes.
function deduplicateTeamSelections(details: HTMLElement) {
  const selectedItemNames = Array.from(details.querySelectorAll('.js-repo-team-selected .js-team-selection')).map(
    el => el.getAttribute('data-name')!,
  )
  const filterableItems = details.querySelectorAll('.js-repo-team-filter-results .js-team-selection')
  for (const item of filterableItems) {
    if (selectedItemNames.includes(item.getAttribute('data-name')!)) {
      item.remove()
    }
  }
}

// This will be called when the details are opened or closed.
// We don't care about the details being opened, but on close we want to commit the selections.
function detailsToggled(details: HTMLElement) {
  // Reset scroll to top of the menu on open/close to reset state
  details.querySelector<HTMLElement>('.js-orgs-teams-list')!.scrollTop = 0
  if (details.hasAttribute('open')) return
  commitSelectedTeams(details)
  // Reset the search
  details.querySelector<HTMLInputElement>('.js-remote-orgs-team-input')!.value = ''
}

// On filter change, we need to save the teams that have been selected.
// Otherwise the filter will replace the selected items from the DOM.
function remoteInputStarted(details: HTMLElement) {
  commitSelectedTeams(details)

  // Hide to bring the search to the forefront
  details.querySelector<HTMLElement>('.js-repo-team-selected')!.hidden = true
}

// When this is called, the contents of the filter menu will have been replaced.
// There is a chance that the filter menu will include options that have been selected already
// as we do not filter on the server (no mechanism to pass via `remote-input`). In this case,
// we want to de-duplicate on the client side.
function remoteInputLoadEnd(remoteInput: HTMLInputElement, details: HTMLElement) {
  // Don't change the selected/filtered items since we had no results by returning early
  const filterSection = details.querySelector<HTMLElement>('.js-repo-team-filter-results')!
  if (filterSection.children.length <= 1 && filterSection.children[0]!.getAttribute('data-empty') === 'true') {
    return
  }

  deduplicateTeamSelections(details)

  // Unhide selected items now that we've finished loading
  // This wouldn't happen if we errored
  const selectedSection = details.querySelector<HTMLElement>('.js-repo-team-selected')!
  selectedSection.hidden = false

  // But make sure the filter section is shown if the query is not empty
  if (remoteInput.value !== '') {
    scrollIntoView(filterSection)
  }
}

on(
  'toggle',
  '.js-teams-details',
  function (event) {
    if (!(event.currentTarget instanceof HTMLElement)) return
    detailsToggled(event.currentTarget)
  },
  {
    capture: true,
  },
)

on(
  'loadstart',
  '.js-orgs-teams-remote-input',
  function (event) {
    remoteInputStarted(event.currentTarget.closest<HTMLElement>('.js-teams-details')!)
  },
  {
    capture: true,
  },
)

on(
  'remote-input-success',
  '.js-orgs-teams-remote-input',
  function (event) {
    const remoteInput = event.currentTarget.querySelector<HTMLInputElement>('.js-remote-orgs-team-input')!
    remoteInputLoadEnd(remoteInput, event.currentTarget.closest<HTMLElement>('.js-teams-details')!)
  },
  {
    capture: true,
  },
)

on(
  'details-menu-selected',
  '.js-orgs-team-menu',
  function (event) {
    const details = event.currentTarget.closest<HTMLElement>('.js-teams-details')!
    setTeamButtonTitle(details)
  },
  {
    capture: true,
  },
)

observe('.js-team-orgs-summary-text', function (element) {
  // Sets the button title on load to be an appropriate value
  const details = element.closest<HTMLElement>('.js-teams-details')!
  setTeamButtonTitle(details)
})
