import {changeValue} from '@github-ui/form-utils'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {onKey} from '@github-ui/onfocus'

function textForDays(details: Element): string | null {
  const dayInputs = Array.from(details.querySelectorAll<HTMLInputElement>('input.js-delivery-day-input'))
  const checkedInputs = dayInputs.filter(input => input.checked)

  if (checkedInputs.length === 0) {
    return null
  }

  if (dayInputs.every(input => input.checked)) {
    return details.getAttribute('data-everyday')!
  }

  const weekdayInputs = dayInputs.filter(input => input.classList.contains('js-delivery-day-weekday'))
  if (weekdayInputs.every(input => input.checked) && checkedInputs.length === weekdayInputs.length) {
    return details.getAttribute('data-weekdays')!
  }

  const weekendInputs = dayInputs.filter(input => input.classList.contains('js-delivery-day-weekend'))
  if (weekendInputs.every(input => input.checked) && checkedInputs.length === weekendInputs.length) {
    return details.getAttribute('data-weekends')!
  }

  if (checkedInputs.length === 2) {
    return `${checkedInputs[0]!.value} and ${checkedInputs[1]!.value}`
  }

  return checkedInputs.map(input => input.value).join(', ')
}

export function handleDayChange(details: Element) {
  let title = textForDays(details)
  const detailsSummary = details.querySelector('.js-delivery-day-summary')
  if (detailsSummary == null) {
    return
  }

  if (title == null) {
    title = detailsSummary.getAttribute('data-default-label')!
  }
  const detailsText = detailsSummary.querySelector('.js-delivery-day-summary-text')
  if (detailsText) {
    detailsText.textContent = title
  }
}

function textForTimes(details: Element): string | null {
  const timeInputs = details.querySelectorAll<HTMLInputElement>('input.js-delivery-time-input:checked')

  if (timeInputs.length === 0) {
    return null
  }

  return Array.from(timeInputs)
    .map(input => input.value)
    .join(', ')
}

function checkFormValidity(): boolean {
  let formValid = true
  formValid = checkChatClientValidity() && formValid
  formValid = checkSlackWorkspaceValidity() && formValid
  formValid = checkSlackChannelValidity() && formValid
  formValid = checkDayInputValidity() && formValid
  formValid = checkTimeInputValidity() && formValid
  return formValid
}

function checkChatClientValidity(): boolean {
  let fieldValid = true
  const clientSelectionInputs = document.querySelectorAll<HTMLInputElement>('.js-reminders-client-selection')
  if (clientSelectionInputs && clientSelectionInputs.length > 0) {
    fieldValid = Array.from(clientSelectionInputs).some(e => e.checked)
    clientSelectionInputs[0]!
      .closest<HTMLElement>('.js-reminders-client-form-group')!
      .classList.toggle('errored', !fieldValid)
  }
  return fieldValid
}

function checkSlackWorkspaceValidity(): boolean {
  let fieldValid = true
  const slackWorkspaceInputs = document.querySelectorAll<HTMLInputElement>('.js-reminders-slack-workspace')
  if (slackWorkspaceInputs.length > 0) {
    const slackDropdown = slackWorkspaceInputs[0]!.closest<HTMLElement>('.js-slack-form-group')
    if (slackDropdown!.hidden) {
      return true
    }
    fieldValid = Array.from(slackWorkspaceInputs).some(e => e.checked)
    slackDropdown!.classList.toggle('errored', !fieldValid)
  }
  return fieldValid
}

function checkSlackChannelValidity(): boolean {
  let fieldValid = true
  const slackChannelInput = document.querySelector<HTMLInputElement>('.js-reminders-slack-channel')
  if (slackChannelInput) {
    const validSlackChannelRegex = /^[a-z0-9_-]+$/
    const slackChannelValue = slackChannelInput.value
    fieldValid = Boolean(
      slackChannelValue.length <= 80 && slackChannelValue.length > 0 && slackChannelValue.match(validSlackChannelRegex),
    )
    const formGroup = slackChannelInput.closest<HTMLElement>('.js-slack-channel-form-group')!
    const errorSelector = formGroup.querySelector<HTMLElement>('.js-slack-channel-error')!
    errorSelector.textContent = errorSelector.getAttribute('data-default-error')!
    formGroup.classList.toggle('errored', !fieldValid)
  }
  return fieldValid
}

function checkDayInputValidity(): boolean {
  let fieldValid = true
  const dayInputs = document.querySelectorAll<HTMLInputElement>('.js-delivery-day-input')

  if (dayInputs.length > 0) {
    const allowBlank = dayInputs[0]?.closest('.js-orgs-reminders-day-menu')?.hasAttribute('data-allow-blank')
    fieldValid = allowBlank || Array.from(dayInputs).some(e => e.checked)
    dayInputs[0]!.closest<HTMLElement>('.js-day-form-group')!.classList.toggle('errored', !fieldValid)
  }
  return fieldValid
}

function checkTimeInputValidity(): boolean {
  let fieldValid = true

  // Require at least one time. If `data-allow-blank` is set, only require a time if a day is selected.
  const daysSelected = Array.from(document.querySelectorAll<HTMLInputElement>('.js-delivery-day-input')).some(
    e => e.checked,
  )
  const timeInputs = document.querySelectorAll<HTMLInputElement>('.js-delivery-time-input')
  if (timeInputs.length > 0) {
    const allowBlank = !!timeInputs[0]?.closest('.js-orgs-reminders-time-menu')?.hasAttribute('data-allow-blank')
    fieldValid = Array.from(timeInputs).some(e => e.checked) || (allowBlank && !daysSelected)
  }

  timeInputs[0]!.closest<HTMLElement>('.js-time-form-group')!.classList.toggle('errored', !fieldValid)
  return fieldValid
}

function handleTimeChange(details: Element) {
  const detailsSummary = details.querySelector<HTMLElement>('.js-delivery-time-summary')!
  const title = textForTimes(details) || detailsSummary.getAttribute('data-default-label')!

  const detailsText = detailsSummary.querySelector<HTMLElement>('.js-delivery-time-summary-text')!
  // Update both the text and the title, the title will show all times on hover
  detailsText.textContent = title
  detailsText.setAttribute('title', title)
}

function setDefaultTimeZone(details: Element) {
  const userTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone || ''

  const toCheck = details.querySelector<HTMLInputElement>(
    `.js-delivery-time-zone-input[data-browser-name="${userTimeZone}"], .js-delivery-time-zone-input[data-browser-name="Etc/UTC"]`,
  )!
  changeValue(toCheck, true)
}

// Handle when there is a change to the selected time zone.
//
// This will try to use the browser's time zone as the default if we're creating a new reminder.
function handleTimeZoneChange(details: Element) {
  const checkedInput = details.querySelector('input.js-delivery-time-zone-input:checked')
  if (checkedInput instanceof HTMLInputElement) {
    const title = checkedInput.getAttribute('data-display-name') || ''
    const detailsText = details.querySelector<HTMLSpanElement>('.js-delivery-time-zone-summary-text')!

    detailsText.textContent = title
  } else {
    setDefaultTimeZone(details)
  }
}

function onClientChange() {
  const clientSelectionInputs = document.querySelectorAll<HTMLInputElement>('.js-reminders-client-selection')
  if (clientSelectionInputs.length > 0) {
    const selectedClient = Array.from(clientSelectionInputs).find(e => e.checked)
    if (selectedClient) {
      clientSelectionInputs[0]!
        .closest<HTMLElement>('.js-reminders-client-form-group')!
        .classList.toggle('errored', false)
      document.querySelector<HTMLElement>('.js-slack-form-group')!.hidden =
        selectedClient.value === 'ReminderTeamsWorkspace'
      document.querySelector<HTMLElement>('.js-real-time-alert-input')!.hidden =
        selectedClient.value === 'ReminderTeamsWorkspace'
      // disable the submit button if an error is present for the selected client, and show the error
      const submitButton = document.querySelector<HTMLInputElement>('.js-reminder-submit-button')
      submitButton!.disabled = showFlashErrorIfPresent(selectedClient.value)
    }
  }
}

// Show/hide client error, if present, based on which client is selected
// Usage:
// * value parameter takes the selected client which currently could be ReminderTeamsWorkspace or ReminderSlackWorkspace
//   based on the value passed the function would hide or show appropriate error message for the selected client
// Working:
// * This function finds client errors that might be present on the page using `.js-reminder-client-error`
//   using the passed value as a differentiator between error it either hides or shows the error
//   this function returns true if any error was present for the selected client
function showFlashErrorIfPresent(value: string): boolean {
  const reminderClientErrors = document.querySelectorAll<HTMLTemplateElement>('.js-reminder-client-error')
  let anyError = false
  for (const reminderClientError of reminderClientErrors) {
    anyError ||= reminderClientError.classList.contains(value)
    reminderClientError.hidden = !reminderClientError.classList.contains(value)
  }
  return anyError
}

on('change', '.js-delivery-day-input', function (event) {
  if (!(event.target instanceof Element)) return
  const details = event.target.closest('.js-orgs-reminders-day-menu')
  if (details == null) {
    return
  }
  handleDayChange(details)
  checkDayInputValidity()
})

on('change', '.js-reminders-slack-workspace', function () {
  checkSlackWorkspaceValidity()
})

onKey('keyup', '.js-reminders-slack-channel', function () {
  checkSlackChannelValidity()
})

on('change', '.js-reminders-slack-channel', function () {
  checkSlackChannelValidity()
})

on('change', '.js-reminders-client-selection', onClientChange)

on('change', '.js-orgs-ignore-approvals-input', function (event) {
  if (!(event.target instanceof Element)) return
  const summaryText = event.target.getAttribute('data-title')!
  document.querySelector<HTMLElement>('.js-orgs-ignore-approvals-summary-text')!.textContent = summaryText
})

on('change', '.js-orgs-ignore-approvals-check', function (event) {
  const details = document.querySelector<HTMLElement>('.js-orgs-ignore-approvals-menu')!
  const hiddenRadio = document.querySelector<HTMLInputElement>('.js-default-orgs-ignore-approvals-input')!
  const target = event.target
  if (target instanceof HTMLInputElement) {
    details.classList.toggle('has-removed-contents', !target.checked)
    hiddenRadio.classList.toggle('has-removed-contents', target.checked)
  }
})

on('change', '.js-delivery-time-input', function (event) {
  if (!(event.target instanceof Element)) return
  const details = event.target.closest<HTMLElement>('.js-orgs-reminders-time-menu')!
  handleTimeChange(details)
  checkTimeInputValidity()
})

/**
 * Show remind author checkbox if require reviews is enabled.
 *
 * @param {Event} event - The event that triggered this call.
 */
on('change', '.js-orgs-needed-reviews-input', function (event) {
  if (!(event.target instanceof Element)) return
  const summaryText = event.target.getAttribute('data-title')!
  document.querySelector<HTMLElement>('.js-orgs-remind-authors-summary-text')!.textContent = summaryText
})

/**
 * Show remind author options if include reviewed PR's is checked.
 *
 * @param {Event} event - The event that triggered this call.
 */
on('change', '.js-orgs-include-reviewed-prs-check', function (event) {
  const target = event.target
  if (!(target instanceof HTMLInputElement)) return
  const details = document.querySelector<HTMLElement>('.js-orgs-remind-authors-menu')!

  details.classList.toggle('has-removed-contents', !target.checked)
})

/**
 * Handle check events for the time zone input radio checkbox.
 *
 * @param {Event} event - The event that triggered this call.
 */
on('change', '.js-delivery-time-zone-input', function (event) {
  if (!(event.target instanceof Element)) return
  const details = event.target.closest<HTMLElement>('.js-orgs-reminders-time-zone-menu')!
  handleTimeZoneChange(details)
})

observe('.js-reminders-client-selection', onClientChange)

// Use the below functions to set the values on load to avoid duplicating logic in the view-controller
observe('.js-orgs-reminders-time-menu', handleTimeChange)
observe('.js-orgs-reminders-time-zone-menu', handleTimeZoneChange)

// Validates the form on submit, blocking if it's not valid
on('submit', 'form.js-reminders-form', function (event) {
  const formValid = checkFormValidity()
  if (formValid === false) {
    event.preventDefault()
    return
  }
  const target = event.currentTarget as HTMLFormElement
  const formSubmitInput = target.querySelector<HTMLInputElement>('.js-reminder-submit-button')
  if (formSubmitInput) {
    formSubmitInput.disabled = true
  }
})

// Show / hide the options input for real time reminders
on('change', '.js-reminders-checkbox-trigger', function (event) {
  const target = event.currentTarget as HTMLInputElement
  const container = target.closest<HTMLElement>('.js-reminders-checkbox-container')!

  const details = container.querySelector('.js-reminders-checkbox-details')
  if (details == null) return

  details.classList.toggle('has-removed-contents', !target.checked)
})

// Simulate details dialog behavior
on('click', '.js-add-slack-workspace', function (event) {
  event.preventDefault()
  const detailsSummary = document.querySelector('.js-add-slack-workspace-details') as HTMLElement
  detailsSummary?.click()
})
